import { smoothScroll } from '../utils';

export default ({navigation}) => {
  if (navigation.length) {
    return (
      <div className="navigation">
        {navigation.map((nav, index) => {
          return (
            <a key={index} className='nav-item' href={`#${index}`} onClick={() => smoothScroll(nav)}>
              {nav.innerText}
            </a>
          );
        })}
      </div>
    );
  } else {
    return [];
  }
};
